import React, { Component } from 'react';
import './index.css';
import Avatar from 'material-ui/Avatar';

const images = {
  avatar: require('../../assets/avatar/cl-min.jpg'),
};

const styles = {
  container: {
  },
  content: {
    maxWidth: 640,
  },
};

class About extends Component {
  render() {
    return (
      <div className="about-container" style={styles.container}>
        <div className="avatar-container">
          <Avatar size={120} alt="avatar" src={images.avatar} />
        </div>
        <div style={styles.content}>
          Hi! I am Chin Loong.
          <br />
          <br />
          I build mobile apps, both iOS and Android for startups, using React Native.
          <br />
          <br />
          Apps built with React Native have reduced significant amount of development time, and therefore cost as well.
          <br />
          <br />
          I enjoy building mobile apps with React Native, it is one of the best solutions for startups to launch faster, for iOS and Android.
          <br />
          <br />
          Finally, welcome to my site where I blog about app development with React Native, and showcase my previous projects.
        </div>
      </div>
    );
  }
}

export default About;



